<template>
    <div class="mainwrapper">
        <div class="shop-cart">
            <div class="content">
                <div class="content-left">
                    <div class="logo-wrapper">
                        <div class="logo" @click="toggleList" :class="{highlight:totalCount>0}">
                            <span class="icon-yahoo"></span>
                        </div>
                        <div class="num" v-show="totalCount>0">{{totalCount}}</div>
                    </div>
                    <div class="price" :class="{highlight:totalPrice>0}">￥{{totalPrice}}</div>
                    <div class="desc">需配送费{{deliveryPrice}}元</div>
                </div>
                <div class="content-right" @click="pay">
                    <div class="pay" :class="payClass">{{payDesc}}</div>
                </div>
            </div >
            <transition name="fold">
                <div class="shopcart-list" v-show="listShow">
                    <div class="list-header">
                        <h1 class="title">购物车</h1>
                        <span class="empty"@click="empty">清空</span>
                    </div>
                    <div class="list-content" ref="listContent">
                        <ul>
                            <li class="food border-1px" v-for="food in selectFoods">
                                <span class="name">{{food.name}}</span>
                                <div class="price">
                                    <span>￥{{food.price * food.count}}</span>
                                </div>
                                <div class="cartcontrol-wrapper">
                                    <v-cartcontrol class="cartcontrol-wrapper" :food="food"></v-cartcontrol>
                                </div>
                            </li>
                        </ul>
                    </div>
                </div>
            </transition>
        </div>
        <transition name="fade">
            <div class="list-mask" v-show="listShow" @click="hideList"></div>
        </transition>
    </div>

</template>

<script type="text/ecmascript-6">
    /* eslint-disable */
    import cartcontrol from './cartcontrol.vue';
    import BScroll from "better-scroll";
    export default{
        data(){
            return{
                fold:true
            }
        },
        props:{
            selectFoods:{
              type:Array,
                default(){
                  return [];
                }
            },
            deliveryPrice:{
                type:Number,
                default:0//设置默认值
            },
            minPrice:{
                type:Number,
                default:0
            }
        },
        computed:{
            totalPrice(){
                var total = 0;
                this.selectFoods.forEach((food) => {
                    total += food.price * food.count;
                });
                return total;
            },
            totalCount(){
                var count = 0;
                this.selectFoods.forEach((food) => {
                    count += food.count;
                });
                return count;
            },
            payDesc(){
                if(this.totalPrice === 0){
                    return `￥${this.minPrice}元起送`;
                } else if (this.totalPrice < this.minPrice) {
                    var diff = this.minPrice - this.totalPrice;
                    return `还差￥${diff}元起送`;
                } else {
                    return '去结算';
                }
            },
            payClass(){
                if(this.totalPrice < this.minPrice){
                    return 'not-enough';
                } else {
                    return 'enough';
                }
            },
            listShow(){
                if(!this.totalCount){
                    this.fold = true;
                    return false;
                }
                var show = !this.fold;
                if(show) {
                    this.$nextTick(() => {
                        if(!this.scroll){
                            this.scroll = new BScroll(this.$refs.listContent,{
                                click:true
                            });
                        } else {
                            this.scroll.refresh();
                        }
                    })
                }
                return show;
            }
        },
        methods:{
            drop(el){
//                console.log(el);
            },
            toggleList(){
                if(!this.totalCount){
                    return;
                }
                this.fold = !this.fold;
            },
            empty(){
                this.selectFoods.forEach((food)=>{
                    food.count = 0;
                })
            },
            hideList(){
                this.fold = true;
            },
            pay(){
                if(this.totalPrice<this.minPrice){
                    return;
                }
                alert(`需要支付${this.totalPrice}元`);
            }
        },
        components:{
            'v-cartcontrol':cartcontrol
        }
    }
</script>

<style lang="stylus" rel="stylesheet/stylus">
    @import "../../static/border_1px.styl"
.shop-cart
    position fixed
    left: 0
    bottom: 0
    z-index 50
    width:100%
    height:48px
    .content
        display flex
        background #141d27
        font-size 0
        .content-left
            flex:1
            .logo-wrapper
                position relative
                display inline-block
                vertical-align top
                position: relative
                top: -10px
                margin 0 12px
                padding: 6px
                width: 56px
                height: 56px
                box-sizing border-box
                border-radius 50%
                background #141d27
                .logo
                    width:100%
                    height:100%
                    background #2b343c
                    border-radius 50%
                    text-align center
                    .icon-yahoo:before
                        content: "\eabb"
                        color: #80858a
                        font-size 24px
                        line-height 44px
                    &.highlight
                        background rgb(10,160,220)
                        .icon-yahoo:before
                            color: #fff
                .num
                    position absolute
                    color: #fff
                    top:0
                    right:0
                    font-size: 9px
                    width: 24px
                    height: 16px
                    line-height 16px
                    text-align center
                    border-radius 16px
                    font-weight 700
                    background rgb(240,20,20)
                    box-shadow 0 4px 8px 0 rgba(0,0,0,.4)
            .price
                display inline-block
                vertical-align top
                line-height 24px
                margin-top 12px
                padding-right 12px
                box-sizing border-box
                border-right 1px solid rgba(255,255,255,.1)
                font-size 16px
                font-weight 700px
                color #80858a
                &.highlight
                    color: #fff
            .desc
                display inline-block
                vertical-align top
                line-height 24px
                margin-top 13px
                padding-left 12px
                box-sizing border-box
                font-size 10px
                font-weight 700px
                color #80858a
        .content-right
            flex 0 0 102
            width:105px
            .pay
                height: 48px
                line-height 48px
                font-size 12px
                font-weight 700
                color: #80858a
                text-align center
                background #2b333b
                &.enough
                    background #00b43c
                    color: #fff
                &.not-enough
                    background #2b333b


    .shopcart-list
        position: absolute
        left:0
        top:0
        z-index -1
        width:100%
        transform translate3d(0,-100%,0)
        transition all .2s
        &.fold-enter,&.fold-leave-active
            transition all .5s
            transform translate3d(0,-100%,0)
        &.fold-enter-active,&.fold-leave-active
            transform translate3d(0,0,0)
        .list-header
            height: 40px
            line-height 40px
            padding:0 18px
            background #f3f5f7
            border-bottom 1px solid rgba(7,17,27,.1)
            .title
                float: left
                font-size 14px
                color: rgb(7,17,27)
            .empty
                float: right
                font-size 12px
                color: rgb(0,160,220)
        .list-content
            padding:0 18px
            max-height 217px
            background #fff
            overflow hidden
            .food
                position: relative
                padding:12px 0
                box-sizing border-box
                border-1px(rgba(7,17,27,.1))
                .name
                    line-height 24px
                    font-size 14px
                    color rgb(7,17,27)
                .price
                    position absolute
                    right 99px
                    bottom:12px
                    font-size 14px
                    font-weight 700
                    color: rgb(240,20,20)
                    line-height 24px
                .cartcontrol-wrapper
                    min-width: 90px
                    position absolute
                    right:0
                    bottom:6px
.list-mask
    position fixed
    top:0
    left: 0
    z-index 31
    width:100%
    height:100%
    //backdrop-filter blur(10px)
    background rgba(7,17,27,.6)
    &.fade-transition
        transition all .5s
        opacity 1
    &.ade-enter-active,&.ade-leave-active
        opacity:0
        background rgba(7,17,27,0)
        //backdrop-filter blur(2px)
</style>



